
<template>
  <a-modal
    class="detail-modal"
    :title="title"
    okText=""
    :visible="visible"
    @cancel="handleCancel"
    @ok="handleCancel"
  >
    <ul v-if="show" class="item-descr-list">
      <li v-for="item in columns" :key="item.title || item.dataIndex">
        <span v-if="item.type === 'title'" class="item-descr-tit" style="color: #333; padding-top: 10px; font-weight: bold;">
          {{ item.title }}
        </span>
        <span v-else-if="item.type === 'html'" v-html="currRecord[item.dataIndex]"></span>
        <template v-else>
          <span class="item-descr-tit">
            {{ item.title }}
          </span>
          <span class="item-descr-txt">
            {{ currRecord[ item.dataIndex ] || '' }}
          </span>
        </template>
      </li>
    </ul>
  </a-modal>
</template>
<script>
import { baseModalMixins } from '@/mixins/modalMixin';
import { getTimeDiff } from "@/utils/util";
export default {
  mixins: [baseModalMixins],
  data() {
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id',
      },
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'status_zh',
      },
      {
        title: '',
        dataIndex: 'createDate',
      },
      {
        title: '',
        dataIndex: 'timeDiff',
      },
      // ===========
      // 
      {
        type: 'title',
        title: ''
      },
      {
        title: '',
        dataIndex: 'hostTypeName',
      },
      {
        title: 'ID',
        dataIndex: 'hostTypeId',
      },
      {
        title: '',
        dataIndex: 'memory',
      },
      {
        title: 'VCPU()',
        dataIndex: 'vcpu',
      },
      {
        title: '',
        dataIndex: 'disk',
      },
      {
        type: 'title',
        title: 'IP',
      },
      {
        title: '',
        type: 'html',
        dataIndex: 'ip',
      },
      // ===========
      // 
      {
        type: 'title',
        title: '',
      },
      {
        title: '',
        type: 'html',
        dataIndex: 'securityGroupsList',
      },
      // ==========
      // 
      {
        type: 'title',
        title: '',
      },
      {
        title: '',
        dataIndex: 'secretKeyName',
      },
      {
        title: '',
        dataIndex: 'imageName',
      },
      {
        title: 'ID',
        dataIndex: 'imageId',
      },

      // ==========
      // 
      {
        type: 'title',
        title: '',
      },
      {
        type: 'html',
        dataIndex: 'cloudHost',
      },

    ];
    return {
      name: 'hostDetail',
      columns,
      show: false,
    };
  },
  computed: {
    title () {
      return ` / ${this.currRecord.name} `
    }
  },
  methods: {
    onShow () {
      this.show = true;
      this.currRecord.securityGroupsList = this.currRecord.securityGroups.map(item2 => {
        return `<p>${item2}</p>`
      }).join('');
      this.currRecord.cloudHost = this.currRecord.mountDisk !== '' ? this.currRecord.mountDisk.map(item => `<p>${item}</p>`).join('') : '';
      this.currRecord.ip = this.currRecord.network.join(', ');
      this.setTimeDiff()
    },
    setTimeDiff () {
      const timeDiff = getTimeDiff(this.currRecord.timestamp)
      this.currRecord.timeDiff = timeDiff;
    }
  }
};
</script>

