<template>
  <div>
    <a-form :form="form">
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-radio-group
          buttonStyle="solid"
          v-decorator="[
            'regionId',
            {
              initialValue: '1',
              rules: [{ required: true, message: '!' }]
            }
          ]"
        >
          <a-radio-button value="1">1</a-radio-button>
          <a-radio-button value="2" disabled>2</a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="CPU">
        <a-radio-group
          buttonStyle="solid"
          v-decorator="[
            'cpu',
            {
              initialValue: 1,
              rules: [{ required: true, message: 'CPU!' }]
            }
          ]"
        >
          <a-radio-button
            v-for="item in optionList.cpu"
            :key="item.value"
            :value="item.value"
          >
            {{ item.text }}
          </a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-radio-group
          buttonStyle="solid"
          v-decorator="[
            'memory',
            {
              initialValue: 1,
              rules: [{ required: true, message: '!' }]
            }
          ]"
        >
          <a-radio-button
            v-for="item in optionList.memory"
            :key="item.value"
            :value="item.value"
          >
            {{ item.text }}
          </a-radio-button>
        </a-radio-group>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-col>
          <a-input-number
            :step="10"
            :min="40"
            :max="200"
            @blur="handleSystemDiskBlur"
            v-decorator="[
              'systemDisk',
              {
                initialValue: 40,
                rules: [{ required: true, message: '!' }]
              }
            ]"
          />
          <span style="margin: 0 5px">G</span>
          <a-tooltip class="tooltip">
            <template slot="title">40G-200G,10G</template>
            <a-icon type="info-circle" />
          </a-tooltip>
        </a-col>
      </a-form-item>
    </a-form>
  </div>
</template>
<script>

const optionList = {
  memory: [
    { text: "1G", value: 1 },
    { text: "2G", value: 2 },
    { text: "4G", value: 4 },
    { text: "8G", value: 8 },
    { text: "12G", value: 12 },
    { text: "16G", value: 16 },
    { text: "32G", value: 32 },
    { text: "48G", value: 48 },
    { text: "64G", value: 64 }
  ],
  cpu: [
    { text: "1", value: 1 },
    { text: "2", value: 2 },
    { text: "4", value: 4 },
    { text: "8", value: 8 },
    { text: "12", value: 12 },
    { text: "24", value: 24 },
    { text: "32", value: 32 }
  ]
};

export default {

  data() {
    return {
      form: this.$form.createForm(this),
      labelCol: { span: 8 },
      wrapperCol: { span: 12 },
      loading: false,
      systemDisk: 1,
      optionList
    };
  },
  methods: {
    handleSubmit() {
      return new Promise((resolve, reject) => {
        this.form.validateFieldsAndScroll((err, values) => {
          err
            ? reject(err)
            : resolve(
                Object.assign({}, values, {
                  memory: values.memory * 1024
                  //  M
                })
              );
        });
      });
    },

    handleSystemDiskBlur(e) {
      //  10 
      let v = e.target.value;
      this.form.setFieldsValue({
        systemDisk: Math.ceil(parseInt(v) / 10) * 10
      });
    }
  }
};
</script>
<style scoped>
.tooltip {
  cursor: pointer;
}
.tooltip:hover {
  color: #1890ff;
}
</style>
