<template>
  <div>
    <page-layout>
      <div class="content">
        <div class="table-operator clearfix" style="margin-bottom: 16px;">
          <a-button icon="sync" :disabled="loading" @click="handleRefresh" style="margin-right: 10px;" title=""></a-button>
          <a-button @click="$router.push({name: 'CreateInstance'})" type="primary" style="margin-right: 10px;" icon="plus">
            
          </a-button>
          <a-button
            type="primary"
            style="margin-right: 10px;"
            icon="play-circle"
            @click="handleMultiMenuClick('launch')"
          >
            
          </a-button>
          <a-button
            type="primary"
            style="margin-right: 10px;"
            icon="poweroff"
            class="shut-down"
            @click="handleMultiMenuClick('shutdown')"
          >
            
          </a-button>
          <a-dropdown style="margin-right: 10px;">
            <a-menu slot="overlay" @click="handleMultiMenuClick($event.key)">
              <a-menu-item
                v-for="item in multiMenuOptions"
                :key="item.id"
              >
                {{ item.name }}
              </a-menu-item>
            </a-menu>
            <a-button style="margin-left: 8px">
              
              <a-icon type="down" />
            </a-button>
          </a-dropdown>

          <span style="float: right;">
            <a-input-group compact class="compact-search-input">
              <a-select @change="v => searchValues.type = v" v-model="searchValues.type" style="width: 90px!important;">
                <a-select-option
                  v-for="(value, key) in types"
                  :value="key"
                  :key="key"
                >
                  {{ value }}
                </a-select-option>
              </a-select>
              <a-input
                style="width: 200px"
                @pressEnter="handleDATA"
                v-model="searchValues.inputValue"
                :placeholder="`${types[searchValues.type]}`"
              >
                <a-icon v-if="searchValues.inputValue" slot="suffix" type="close-circle" @click="searchValues.inputValue = ''" />
              </a-input>
            </a-input-group>

            <a-select @change="v => searchValues.status = v" v-model="searchValues.status" style="margin-left: 8px; width: 90px;">
              <a-select-option key="all" value="all"></a-select-option>
              <a-select-option key="build" value="build"></a-select-option>
              <a-select-option key="active" value="active"></a-select-option>
              <a-select-option key="paused" value="paused"></a-select-option>
              <a-select-option key="shutoff" value="shutoff"></a-select-option>
              <a-select-option key="reboot" value="reboot"></a-select-option>
            </a-select>
            <a-button type="primary" @click="handleDATA" style="margin-left: 8px">
              
              <a-icon type="search" />
            </a-button>
          </span>
        </div>
        <a-alert type="info" showIcon style="margin-bottom: 16px;">
          <div slot="message">
            &nbsp;
            <a style="font-weight: 600">{{ selectedRowKeys.length }}</a>&nbsp;&nbsp;
            <a
              v-show="selectedRowKeys.length"
              style="margin-left: 24px"
              @click="selectedRowKeys = []"
            >
              
            </a>
          </div>
        </a-alert>
        <a-table
          :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onTableSelectChange}"
          :columns="columns"
          :rowKey="record => record.id"
          :dataSource="data"
          :pagination="pagination"
          :loading="loading"
          @change="handleTableChange"
        >
          <template slot="id" slot-scope="id, record">
            <a @click="handleSingleMenuClick('hostDetail', record)" title="">{{ id.substring(0, 8) }}</a>
          </template>

          <template slot="securityGroups" slot-scope="group, record">
            <a-spin v-if="record.loadingGroup">
              <a-icon slot="indicator" type="loading-3-quarters" style="font-size: 12px" spin />
            </a-spin>
            <template v-else>
              <template v-if="Array.isArray(group) && group.length">
                <p v-for="(item, index) in group" :key="index + item">{{ item }}</p>
              </template>
              <template v-else>
                
              </template>
            </template>
          </template>

          <template slot="ipAddress" slot-scope="ipAddress, record">
            <a-spin v-if="record.loadingNetwork">
              <a-icon slot="indicator" type="loading-3-quarters" style="font-size: 12px" spin />
            </a-spin>
            <template v-else>
              {{ ipAddress || '' }}
            </template>
          </template>
          <template slot="network" slot-scope="network, record">
            <a-spin v-if="record.loadingNetwork">
              <a-icon slot="indicator" type="loading-3-quarters" style="font-size: 12px" spin />
            </a-spin>
            <template v-else>
              <template v-if="Array.isArray(network) && network.length">
                <p v-for="item in network" :key="item">{{ item }}</p>
              </template>
              <template v-else>
                
              </template>
            </template>
          </template>

          <template slot="status" slot-scope="text, record">
            <p>
              <span :class="{ 'status-disabled': record.taskState }">{{ record.status_zh }}</span>
              <a-spin v-if="record.taskState">
                <a-icon slot="indicator" type="loading-3-quarters" style="font-size: 12px" spin />
              </a-spin>
            </p>
          </template>

          <template slot="operation" slot-scope="options, record">
            <a-dropdown style="margin-right: 10px;">
              <a-menu slot="overlay" @click="handleBeforeSingleMenuClick($event.key, record)">
                <a-menu-item
                  v-for="item in record.singleMenuOptions"
                  :key="item.id"
                  :disabled="item.disabled"
                >
                  {{ item.name }}
                </a-menu-item>
              </a-menu>
              <a-button style="margin-left: 8px" :disabled="!Boolean(record.singleMenuOptions.length)">
                
                <a-icon type="down" />
              </a-button>
            </a-dropdown>
          </template>
        </a-table>
      </div>
    </page-layout>

    <multi-launch />
    <multi-shutdown />
    <multi-delete />
    <multi-restart />
    <multi-soft-restart />

    <create-snapshot-modal />
    <bind-i-p-modal />
    <unbind-i-p />
    <delete />
    <!-- <allot-IP-modal /> -->
    <rebuild-cloud-host-modal />
    <edit-security-group-modal />
    <overview-modal />
    <console-panel />
  </div>
</template>

<script>
import PageLayout from "@/components/Layout/PageLayout";

import MultiLaunch from "./Modals/MultiLaunch";
import MultiShutdown from "./Modals/MultiShutdown";
import MultiRestart from "./Modals/MultiRestart";
import MultiDelete from "./Modals/MultiDelete";
import MultiSoftRestart from "./Modals/MultiSoftRestart";

import CreateSnapshotModal from "./Modals/CreateSnapshot";
import BindIPModal from "./Modals/BindIP";
import UnbindIP from "./Modals/UnbindIP";
import Delete from "./Modals/Delete";
// import AllotIPModal from "./Modals/AllotIP";
import RebuildCloudHostModal from "./Modals/RebuildCloudHost";
import EditSecurityGroupModal from "./Modals/EditSafetyGroup";
import OverviewModal from "./Modals/Overview";
import ConsolePanel from './Modals/ConsolePanel'

import tablePageMixins from "@/mixins/tablePageMixins";

import { getinstanceList as getList, getInstanceStatus, getInstanceGroup, getInstanceNetwork } from "@/api/compute/instance";
import { getIPList } from "@/api/network/ip";

import { getTimestamp } from "@/utils/util";

import instance from '@/i18n/zh/instance'
const statusDicts = instance.instance.status;

export default {
  mixins: [tablePageMixins],
  components: {
    PageLayout,
    MultiLaunch,
    MultiShutdown,
    MultiRestart,
    MultiDelete,
    MultiSoftRestart,

    CreateSnapshotModal,
    BindIPModal,
    UnbindIP,
    Delete,
    // AllotIPModal,
    RebuildCloudHostModal,
    EditSecurityGroupModal,
    OverviewModal,
    ConsolePanel,
  },
  data() {
    return {
      getList,
      module: "compute",
      id: "instance",
      name: "",
      selectedRowData: null, // 
      showAllotIPModal: false,
      selectedOperationKey: 0,

      searchType: 'name',

      searchValues: {
        type: 'name',
        inputValue: '',
        status: 'all',
      },
      types: {
        name: '',
        network: '',
        ipAddress: 'IP',
      },


      ipList: [],
      isFetchIpList: false,
    };
  },
  mounted () {
    this.$store.commit[`${this.$parent.id}/clearModal`];
    this.fetchIPList();
  },
  methods: {
    async fetchIPList () {
      this.isFetchIpList = true;
      try {
        const resp = await getIPList();
        this.ipList = resp.data;
      } catch (error) {

      } finally {
        this.isFetchIpList = false;
      }
    },
    handleRefresh() {
      this.searchValues = {
        type: 'name',
        inputValue: '',
        status: 'all',
      }
      this.pagination = Object.assign({}, this.pagination, this.initPagination);
      this.fetch();
    },
    __handleFilterOptions ({ status, ipAddress }) {
      // 
      if( ['build', 'reboot'].includes(status) ) {
        return [];
      }
      // 
      const options = JSON.parse(JSON.stringify(this.singleMenuOptions))

      //  availableStatus 
      const result = options.filter(({ id, availableStatus=[]}, index) => {
        if (availableStatus.length === 0) {
          return true;
        }
        return availableStatus.includes(status);
      })

      // IPIP;
      // IP
      const id = ipAddress === '' || ipAddress === '-' || !ipAddress ? 'unbindIP' : 'bindIP'
      const index = result.findIndex(item => item.id === id);
      result.splice(index, 1);
      return result;
    },
    __handleTransformToZh (status) {
      return statusDicts[status] || status
    },
    async _fetchNetwork (item) {
      try {
        item.loadingNetwork = true;
        const resp = await getInstanceNetwork(item.id);
        Object.assign(item, resp);
        item.singleMenuOptions = [ ...this.__handleFilterOptions(item) ]
      } catch (error) {

      } finally {
        item.loadingNetwork = false;
      }
    },
    async _fetchGroup (item) {
      try {
        item.loadingGroup = true;
        const resp = await getInstanceGroup(item.id);
        Object.assign(item, resp);
      } catch (error) {

      } finally {
        item.loadingGroup = false;
      }
    },
    handleParseData (data) {
      // 
      data.forEach(item => {
        this._fetchNetwork(item);
        this._fetchGroup(item);
        item.securityGroups = [];
        item.network = [];
        item.ipAddress = '';
      })

      // 
      const newData = data.map(item => {
        Object.keys(item).forEach(key => {
          item[key] = item[key] || '';
        })
        let memory = item.memory >= 1024 ? `${item.memory / 1024}G` : `${item.memory}MB`;
        let vcpu = item.vcpu + '';
        let disk = item.disk + 'G';
        let spec = vcpu + memory;
        let status = item.status.toLowerCase();

        // 
        let status_zh = this.__handleTransformToZh(item.status);

        Object.assign(item, {
          memory, vcpu, spec, disk, status_zh,
          singleMenuOptions: [ ...this.__handleFilterOptions(item) ],
          taskState: '',
        })
        return item;
      });
      return newData;
    },
    handleFetchSuccess () {
      const processingStatus = [ 'build', 'rebuild' ]
      const list = this.data.filter(item => processingStatus.includes(item.status));
      list.forEach(item => {
        this.handleTraceStatus(item.id);
      })
    },
    __handleUpdateStatus({ id, status, taskState }) {
      // 
      const currItem = this.data.find(item => item.id === id);
      const currStatus = (status || currItem.status).toLowerCase();
      const options = this.__handleFilterOptions(currItem);
      Object.assign(currItem, {
        status: currStatus,
        taskState: taskState,
        status_zh: this.__handleTransformToZh(currStatus),
      })

      // 
      currItem.singleMenuOptions = this.__handleFilterOptions(currItem);

      this.$nextTick();
    },
    handleTraceStatus (id) {
      this.__handleUpdateStatus({ id, taskState: 'Start' });
      this.__queryListStatus(id)
    },
    async __queryListStatus (id) {
      const resp = {};
      try {
        const data = await getInstanceStatus(id);
        Object.assign(resp, data);

        if (resp.status.toLowerCase() === 'deleted') {
          this.__handleDeleteInstance(id);
          return;
        }
        if (resp.taskState) {
          this.__queryListStatus(id);
        }
        this.__handleUpdateStatus(resp);
      } catch (error) {
        if (error.response.status == 404) {
          this.__handleDeleteInstance(id);
        }
      }
    },
    __handleDeleteInstance (id) {
      // 
      const currItem = this.data.find(item => item.id === id);
      Object.assign(currItem, {
        status: 'deleted',
        taskState: false,
        status_zh: '',
        singleMenuOptions: [],
      });

      // 
      const currIndexInAll = this.allData.indexOf(currItem);
      this.allData.splice(currIndexInAll, 1);
      this.handleDATA();
      this.pagination.total = this.allData.length;

      // 
      const index = this.selectedRowKeys.indexOf(id);
      this.selectedRowKeys.splice(index, 1);

      this.$nextTick();
    },
    handleBeforeSingleMenuClick (key, record) {
      if (key === 'console') {
        window.open(`/console.html?instanceId=${record.id}`);
      } else {
        this.handleSingleMenuClick(key, record)
      }
    },
  }
};
</script>

<style scoped>
.status-disabled {
  user-select: none;
  color: #BBB;
}
.compact-search-input {
  display: inline-block;
  width: auto;
