<template>
  <div>
    <page-layout>
      <div slot="header">
        <p style="margin: 0;">
          Image 
        </p>
        <a-tabs
          defaultActiveKey="imageSource"
          @change="handleTabChange"
          style="position:relative; top: 17px;"
        >
          <a-tab-pane tab="" key="0"></a-tab-pane>
          <a-tab-pane tab="" key="1"></a-tab-pane>
        </a-tabs>
      </div>
      <div class="content">
        <div class="table-operator" style="margin-bottom: 16px;">
          <a-row type="flex" justify="space-between">
            <a-col>
              <p v-show="imageSource == 0">LinuxWindows</p>
              <p v-show="imageSource == 1"></p>
            </a-col>
            <a-col>
              <a-row type="flex" justify="space-between">
                <a-button icon="sync" :disabled="loading" @click="handleRefresh" style="margin-right: 10px;" title=""></a-button>
                <a-button
                  type="primary"
                  style="margin-right: 10px;"
                  icon="plus"
                  @click="handleSingleMenuClick('create')"
                >
                  
                </a-button>
              </a-row>
            </a-col>
          </a-row>
        </div>
        <a-alert type="info" showIcon style="margin-bottom: 16px;">
          <div slot="message">
            &nbsp;
            <a style="font-weight: 600">{{ selectedRowKeys.length }}</a>&nbsp;&nbsp;
            <a style="margin-left: 24px" @click="handleClearSelected"></a>
          </div>
        </a-alert>
        <a-table
          :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onTableSelectChange}"
          :columns="columns"
          :rowKey="record => record.id"
          :dataSource="data"
          :pagination="pagination"
          :loading="loading"
          @change="handleTableChange"
        >
          <template slot="detail" slot-scope="text, record">
            <a title="" @click="handleSingleMenuClick('detail', record)">{{ text.substring(0, 8) }}</a>
          </template>

          <template slot="operation" slot-scope="text, record">
            <a-dropdown style="margin-right: 10px;">
              <a-menu slot="overlay" @click="handleSingleMenuClick($event.key, record)">
                <a-menu-item
                  v-for="item in singleMenuOptions"
                  :key="item.id"
                >
                  {{ item.name }}
                </a-menu-item>
              </a-menu>
              <a-button style="margin-left: 8px">
                
                <a-icon type="down" />
              </a-button>
            </a-dropdown>
          </template>
        </a-table>
      </div>
    </page-layout>
    <create-modal />
    <edit-modal />
    <delete />
    <detail-modal />
    <update />
  </div>
</template>

<script>
import CreateModal from "./Modal/Create";
import EditModal from "./Modal/Edit";
import Delete from "./Modal/Delete";
import DetailModal from "./Modal/Detail";
import Update from "./Modal/Update";
import PageLayout from "@/components/Layout/PageLayout";

import tablePageMixins from "@/mixins/tablePageMixins";

import { getImageList as getList } from "@/api/compute/images";

import images from '@/i18n/zh/images';
const statusDicts = images.images.status

export default {
  mixins: [tablePageMixins],
  components: {
    CreateModal,
    EditModal,
    Delete,
    DetailModal,
    Update,
    PageLayout
  },
  data() {
    return {
      getList,
      module: "compute",
      id: "mirror",
      name: "",
      imageSource: 0,
      selectedRowData: {},
      imageFormatList: [
        { label: "", value: "raw" },
        { label: "VHD - ", value: "vhd" },
        { label: "VMDK - ", value: "vmdk" },
        { label: "VDI - ", value: "vdi" },
        { label: "ISO - ", value: "iso" },
        { label: "QCOW2 - QEMU ", value: "qcow2" },
        { label: "AKI - Amazon ", value: "aki" },
        { label: "ARI - Amazon Ramdisk ", value: "ari" },
        { label: "AMI - Amazon ", value: "ami" },

        // { label: "OVA - ", value: "ova" },
        // { label: "PLOOP - Virtuozzo/", value: "ploop" },
        // { label: "Docker", value: "docker" },
      ],
    };
  },
  computed: {
    columns() {
      const data = this.$store.state[this.module][this.id].columns;
      // 
      return this.imageSource == 0 ? data.slice(0, -1) : data;
    },
  },
  methods: {
    handleTabChange(v) {
      // [0: 1: 2:]
      this.imageSource = v;
      this.data = [];
      this.fetch();
    },
    __handleTransformToZh (status) {
      return statusDicts[status.toLowerCase()] || status
    },

    /**
     * limit  b 
     */
    formatStorageSpace (limit) {
      //  1 KB B
      //  1 MB KB
      //  1 GB MB
      //  GB
      const map = [
        { suffix: 'B',  value: 1,                      maxValue: 1*1024,                },
        { suffix: 'KB', value: 1 *1024,               maxValue: 1*1024 *1024,         },
        { suffix: 'MB', value: 1 *1024 *1024,        maxValue: 1*1024*1024*1024,  },
        { suffix: 'GB', value: 1 *1024 *1024 *1024, maxValue: Infinity,                  },
      ];
      const item = map.find(item => item.maxValue > limit);
      const ret = (limit / item.value).toFixed(2) + item.suffix;

      return ret;

    },

    handleParseData (data) {
      const ret = data.filter(item => {
        return this.imageSource == 0 && item.isPublic === 'public'
          || this.imageSource == 1 && item.isPublic !== 'public';
      })
      const { imageFormatList } = this;
      ret.forEach(item => {
        const imageFormat_zh = (imageFormatList.find(aaa => aaa.value === item.imageFormat) || {}).label;
        const status_zh = this.__handleTransformToZh(item.status)
        const imageType_zh = item.imageType === 'snapshot' ? '' : '';
        const capacity_zh = this.formatStorageSpace(item.capacity == '' ? 0 : item.capacity)
        Object.assign(item, {
          imageFormat_zh,
          status_zh,
          imageType_zh,
          capacity_zh,
          minMemory: item.minMemory / 1024
        })
      })
      return ret;
    },
  }
};
</script>

<style scoped>
.ant-tabs-bar {
  margin: 0;
}
</style>
