
<template>
  <div>
    <a-modal
      @cancel="handleCancel"
      @ok="handleCreate"
      :visible="visible"
      :confirmLoading="confirmLoading"
      title=""
      okText=""
    >
      <a-form class="mirror-create-form" :form="form">
        <a-form-item
          label=""
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
        >
          <a-input
            placeholder=""
            v-decorator="[
              'name',
              {
                rules: [
                  { required: true, message: '!' },
                  rulesObj.name,
                ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item
          label=""
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
        >
          <a-textarea
            placeholder=""
            v-decorator="[
              'description',
              {
                rules: [ rulesObj.desc, ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item
          label=""
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
        >
          <a-select
            :getPopupContainer="getPopupContainer"
            @change="handleSelectSource"
            placeholder=""
            v-decorator="[
              'imageSource',
              {
                initialValue: sourceType,
                rules: [{ required: true, message: '' }]
              }
            ]"
          >
            <!-- [0: 1:] -->
            <a-select-option :value="1"></a-select-option>
            <a-select-option :value="0"></a-select-option>
          </a-select>
        </a-form-item>

        <a-form-item
          v-if="sourceType == 0"
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label=""
        >
          <uploader ref="uploader"></uploader>
        </a-form-item>
        <a-form-item
          v-if="sourceType == 1"
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label=""
        >
          <a-input
            placeholder=""
            v-decorator="[
              'imageUrl',
              {
                rules: [
                  { required: true, message: '' },
                  rulesObj.http
                ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label=""
        >
          <a-select
            :getPopupContainer="getPopupContainer"
            placeholder=""
            v-decorator="[
              'imageFormat',
              { rules: [{ required: true, message: '!' }] }
            ]"
          >
            <a-select-option
              v-for="item in imageFormatList"
              :value="item.value"
              :key="item.value"
            >
              {{ item.label }}
            </a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label=""
        >
          <a-select
            placeholder=""
            :getPopupContainer="getPopupContainer"
            v-decorator="[
              'architecture',
              {
                initialValue: 'bare',
                rules: [{ required: true, message: '!' }]
              }
            ]"
          >
            <a-select-option
              v-for="item in containerFormatList"
              :value="item"
              :key="item"
            >
              {{ item }}
            </a-select-option>
          </a-select>
        </a-form-item>
        <a-form-item
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label="(G)"
        >
          <a-input-number
            :min="1"
            :max="200"
            v-decorator="[
              'disk',
              { rules: [{ required: true, message: '!' }] }
            ]"
          />
        </a-form-item>
        <a-form-item
          :labelCol="formItemLayout.labelCol"
          :wrapperCol="formItemLayout.wrapperCol"
          label="(G)"
        >
          <a-input-number
            :min="1"
            :max="64"
            v-decorator="[
              'memory',
              { rules: [{ required: true, message: '!' }] }
            ]"
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>
<script>
import uploader from "./Uploader";
import { baseModalMixins, formModalMixins } from "@/mixins/modalMixin";
import { createImage as fetchAPI } from '@/api/compute/images';
import rulesObj from '@/utils/rules'
export default {
  mixins: [baseModalMixins, formModalMixins],
  data() {
    return {
      rulesObj,
      fetchAPI,
      name: "create",
      fileList: [],
      sourceType: 1,
      // [0: 1:]

      // 
      containerFormatList: [
        'bare', 'ami', 'ari', 'aki', 'docker', 'ova', 'ovf',
      ],
    };
  },
  components: {
    uploader,
  },
  computed: {
    imageFormatList () {
      return this.$parent.imageFormatList;
    }
  },
  methods: {
    onShow () {
      this.sourceType = 1;
      this.$refs.uploader && this.$refs.uploader.clearFileList();
    },
    handleSelectSource(value) {
      this.sourceType = value;
    },

    handleCreate() {
      const self = this;
      this.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          self.formValues = Object.assign({}, self.formValues, values, {
            memory: values.memory * 1024,
          });
          if (self.sourceType === 0) {
            self.formValues.imageUrl = self.$refs.uploader.file.url;
            if (!self.formValues.imageUrl) {
              this.$message.warn('')
              return;
            }
          }
          self.handleFetch();
        }
      });
    },
    getPopupContainer() {
      return document.querySelector('.mirror-create-form')
    }
  }
};
</script>

