<template>
  <div>
    <a-form :form="form">
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-radio-group
          buttonStyle="solid"
          v-decorator="[
            'billingRule',
            {
              initialValue: 0,
              rules: [{ required: true, message: '!' }]
            }
          ]"
        >
          <!-- [0: 1:] -->
          <a-radio :value="0"></a-radio>
          <a-radio :value="1"></a-radio>
        </a-radio-group>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-input
          v-decorator="[
            'hostName',
            {
              rules: [
                { required: true, message: '!' },
                rulesObj.name
              ]
            }
          ]"
          style="width: 250px"
          placeholder=""
        />
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-input-number
          style="width: 250px"
          :min="1"
          :max="10"
          v-decorator="[
            'hostNumber',
            {
              initialValue: 1,
              rules: [{ required: true, message: '!' }]
            }
          ]"
        />
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-radio-group
          buttonStyle="solid"
          @change="handleChangeLoginType"
          v-decorator="[
            'loginWay',
            {
              initialValue: loginWay,
              rules: [{ required: true, message: '!' }]
            }
          ]"
        >
          <a-radio :value="0">SSH</a-radio>
          <a-radio :value="1"></a-radio>
        </a-radio-group>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="">
        <a-select
          mode="tags"
          @change="handleSelectUserName"
          v-decorator="[
            'userName',
            {
              rules: [
                { required: true, message: '!' },
                rulesObj.name
              ]
            }
          ]"
          style="width: 250px"
        >
          <a-select-option v-for="name in userNameList" :key="name" :value="name">{{ name }}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item
        v-if="loginWay == 1"
        :labelCol="labelCol"
        :wrapperCol="wrapperCol"
        label=""
      >
        <a-input
          type="password"
          autocomplete="new-password"
          v-decorator="[
            'password',
            {
              rules: [
                { required: true, message: '!' },
                rulesObj.password,
              ]
            }
          ]"
          style="width: 250px"
          placeholder=""
        />
      </a-form-item>
      <a-form-item
        v-if="loginWay == 1"
        :labelCol="labelCol"
        :wrapperCol="wrapperCol"
        label=""
      >
        <a-input
          type="password"
          autocomplete="new-password"
          v-decorator="[
            'password2',
            {
              rules: [
                { required: true, message: '!', },
                { validator: confirmPassword,}
              ]
            }
          ]"
          style="width: 250px"
          placeholder=""
        />
      </a-form-item>
      <a-form-item
        :labelCol="labelCol"
        :wrapperCol="wrapperCol"
        v-if="loginWay == 0"
        label="SSH"
      >
        <!-- <div>
          <span style="margin-right: 3px;"></span>
          <a @click="createKeypair"></a>
        </div> -->
        <a-spin
          :spinning="loading"
          tip="..."
          style="width: 250px; display: inline-block;"
        >
          <a-select
            v-decorator="[
              'secretKey',
              {rules: [{ required: true, message: '!' }]}
            ]"
          >
            <a-select-option
              v-for="item in keypairList"
              :key="item.name"
              :value="item.name"
              style="display: block;"
            >
              {{ item.name }}
            </a-select-option>
          </a-select>
        </a-spin>
        <a @click="createKeypair" style="margin-left: 8px;"></a>
      </a-form-item>
      <a-form-item :labelCol="labelCol" :wrapperCol="wrapperCol" label="" style="margin-bottom: 5px;">
        <a-radio-group
          @change="e => userData = e.target.value"
          v-decorator="[
            'userDataType',
            {
              initialValue: 0,
              rules: [{ required: true, message: '!' }]
            }
          ]"
        >
          <!-- [0: 1: 2:] -->
          <a-radio :value="0"></a-radio>
          <a-radio :value="1"></a-radio>
        </a-radio-group>
      </a-form-item>
      <a-row style="margin-bottom: 24px;">
        <a-col :offset="8" :span="12" style="text-align: left;">
          <a-form-item
            v-show="userData == 1"
          >
            <a-upload
              :multiple="true"
              accept=".txt,.sh"
              :remove="handleRemoveFile"
              :beforeUpload="beforeUpload"
              @change="handleFileChange"
            >
              <a-button>
                <a-icon type="upload" />
              </a-button>
            </a-upload>
            <a-textarea
              v-decorator="[ 'userDataText', ]"
              placeholder=" sh  UTF-8."
              :rows="6"
            />
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
    <create-keypair :visible="showCreateKeypairModal" />
    <download-keypair :visible="showDownloadKeypairModal" />
  </div>
</template>
<script>
import CreateKeypair from './modal/CreateKeypair'
import DownloadKeypair from './modal/DownloadKeypair'

import { getKeyPairList } from "@/api/compute/keypair";
import rulesObj from '@/utils/rules'

export default {
  props: {
    defaultUserName: {
      type: String,
      default: ''
    }
  },
  components:  { DownloadKeypair, CreateKeypair },
  data() {
    return {
      rulesObj,
      form: this.$form.createForm(this),
      labelCol: { span: 8 },
      wrapperCol: { span: 12 },

      userData: 0,
      fileList: [],

      keypairList: [],
      loading: false,

      loginWay: 0,

      showCreateKeypairModal: false,
      showDownloadKeypairModal: false,
      createKeypairData: {},
    };
  },
  mounted() {
    this.fetchKeyPairList();
  },
  computed: {
    userNameList () {
      return this.defaultUserName ? [ this.defaultUserName ] : [];
    }
  },
  methods: {
    async fetchKeyPairList () {
      this.loading = true;
      try {
        const resp = await getKeyPairList();
        this.keypairList = resp.data;
        this.form.setFieldsValue({
          secretKey: this.createKeypairData.name || ''
        })
      } catch (err) {

      } finally {
        this.loading = false;
      }
    },
    createKeypair () {
      this.showCreateKeypairModal = true;
    },
    handleSelectUserName (value) {
      // 
      if (value.length > 1) {
        value.splice(0, value.length - 1);
      }
    },
    handleChangeLoginType(e) {
      this.loginWay = e.target.value;
    },
    handleSubmit() {
      return new Promise((resolve, reject) => {
        this.form.validateFieldsAndScroll((err, values) => {
          err ? reject(err) : resolve(Object.assign({}, values, {
            userName: values.userName[0]
          }));
        });
      })
    },
    handleRemoveFile () {
      this.form.setFieldsValue({
        userDataText: ''
      })
    },
    beforeUpload(file) {
      var reader = new FileReader();
      reader.onload = (event) =>{
        this.form.setFieldsValue({
          userDataText: event.target.result
        })
      };
      reader.onerror = (err) => {
        console.log(err);
      }
      reader.readAsText(file, "UTF-8");
      return false;
    },

    handleFileChange(info) {
      info.fileList.splice(0, info.fileList.length - 1);
    },
    confirmPassword (rule, value, callback) {
      const { getFieldValue } = this.form
      if (value && value !== getFieldValue('password')) {
          callback('')
      }
      // callback 
      // 
      callback()
    }
  }
};
</script>

