
<template>
  <div>
    <a-modal
      :visible="visible"
      @cancel="handleCancel"
      @ok="handleCreate"
      :confirmLoading="confirmLoading"
      title=""
      type="danger"
      okText=""
      okType="danger"
    >
      <p><a-icon type="warning" style="color: #faad14;" /> </p>
      <p style="margin-top: 10px;"> {{ list.join(', ') }}</p>

      <!-- <template slot="footer">
        <template v-if="!showMyFooter">
          <a-button @click="handleCancel"></a-button>
          <a-button @click="handleCreate" :loading="confirmLoading" type="danger"></a-button>
        </template>
        <template v-else>
          <a-button @click="handleClose"></a-button>
        </template>
      </template> -->
    </a-modal>
  </div>
</template>
<script>
import { baseModalMixins } from "@/mixins/modalMixin";
import { deleteKeyPair as fetchAPI } from '@/api/compute/keypair';
export default {
  mixins: [baseModalMixins],
  data() {
    return {
      fetchAPI,
      name: "multiDeleta",
      // showMyFooter: false,
      list: [],
      listLength: 0,
    };
  },
  methods: {
    onShow () {
      // this.showMyFooter = false;
      this.list = this.$parent.selectedRowKeys;
      this.listLength = this.list.length;
    },
    handleFetch() {
      /**
       * 
       * Promise.all 
       */
      if (!this.list.length) { return;}
      this.confirmLoading = true;
      this.list.forEach(item => {
        this.handleDelete(item);
      })
    },
    handleFetchEnd () {
      // 
      this.confirmLoading = false;
      // this.showMyFooter = true;
      this.handleCancel();
      this.handleRefreshParentTable();
      this.$message.success('');
    },
    async handleDelete (item) {
      try {
        const payload = {secretKeyId: item}
        const resp = await this.fetchAPI(payload);
      }
      catch (err) {

      }
      finally {
        this.listLength = this.listLength - 1;
        if (this.listLength === 0) {
          this.handleFetchEnd();
        }
      }
    },
  }
};
</script>

